.MenuBar {
    background-color: #1c2d36;
    color: lightgrey;
    margin: auto;
    padding: 0.25rem 1rem 0.25rem 1rem;
    font-family: sans-serif;
    display: flex;
    position: relative;
}

.navContainer {
    display: flex;
    margin-left: 2rem;
    align-items: center;
    font-size: 1.25em;
}

.navContainer a {
    display: flex;
    height: 60%;
    align-items: center;
    border-radius: 10px;
    padding: 0.25rem 0.5rem;
    text-decoration: none;
    color: whitesmoke;

}

.navContainer a span {
    padding-left: 0.4rem;
}

.navContainer a:hover {
    background-color:black;
    color: lightpink
}